// colors
[data-dark="false"] {
  --primary: #cc0000;
  --secondary: #eeaeca;
  --text: #2d2926;
  --background: #ffffff;
  --background-alt: #EEAECA;
  --background-alt: radial-gradient(circle,rgba(238, 174, 202, .4) 0%, rgba(148, 187, 233, .4) 100%);
  --light-gray: rgba(45, 41, 38, .125); 
  --gray: rgba(45, 41, 38, .5); 
  --dark-gray: rgba(45, 41, 38, .75); 
  --overlay: #00000020;
}

[data-dark="true"] {
  --primary: #eddd53; 
  --secondary: #cc0000;
  --text: #ffffff;
  --background: #242629dd;
  --background-alt: #247BA0;
  --background-alt: radial-gradient(circle, rgba(36, 123, 160, .4) 0%, rgba(27, 153, 139, .4) 100%);
  --light-gray: #ffffff40; //#404040;
  --gray: #ffffff80; //#808080;
  --dark-gray: #ffffffb0; //#b0b0b0;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; //"Inter", sans-serif;
  --heading: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; // "Inter", sans-serif;
  --body: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; // "Inter", sans-serif;
  --code: "Fira Code", monospace;

  // font sizes
  --large: 1.2rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 1.5;
  --compact: 1.3;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
  --transition: 0.2s ease;

  // antialiasing
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
